import { useEffect } from 'react';
import * as THREE from 'three';

const ThreeComponent = () => {
  let camera: THREE.PerspectiveCamera = new THREE.PerspectiveCamera(70, window.innerWidth / window.innerHeight, 0.01, 10);
  let scene: THREE.Scene = new THREE.Scene();
  let renderer: THREE.WebGLRenderer;
  let geometry: THREE.Geometry | THREE.BufferGeometry = new THREE.BoxGeometry(0.3, 0.3, 0.3);
  let material: THREE.Material | THREE.Material[] = new THREE.MeshNormalMaterial();
  let mesh: THREE.Mesh = new THREE.Mesh(geometry, material);;

  camera.position.z = 1;

  const init = () => {
    material = new THREE.MeshNormalMaterial();

    mesh = new THREE.Mesh(geometry, material);
    scene.add(mesh);

    renderer = new THREE.WebGLRenderer({ antialias: true, alpha: true });
    renderer.setSize(window.innerWidth, window.innerHeight);
    document.querySelector('#threed').appendChild(renderer.domElement);
  };

  const animate = () => {
    requestAnimationFrame(animate);
    mesh.rotation.x += 0.01;
    mesh.rotation.y += 0.03;
    renderer.render(scene, camera);
  };

  useEffect(() => {
    setTimeout(() => {
      init();
      animate();
    }, 100)

    return () => {

    };
  }, []);

  return <div id="threed" />;
};

export default ThreeComponent;
